.section{padding:2rem 1rem;background:linear-gradient(to right, #fff, #f8fafc);color:#333}@media(min-width: 768px){.section{padding:3rem 2rem}}@media(min-width: 1024px){.section{padding:4rem 6rem}}.header{display:flex;flex-direction:column;align-items:center;margin-bottom:2rem}@media(min-width: 768px){.header{flex-direction:row;align-items:start}}.logoContainer{flex-shrink:0;margin-bottom:1.5rem;display:flex;justify-content:center}@media(min-width: 768px){.logoContainer{margin-bottom:0;margin-right:2rem;justify-content:flex-start}}.logoContainer img{max-width:150px;height:auto}.textContent h2{font-weight:700;margin-bottom:.75rem;font-size:1.875rem;background:linear-gradient(90deg, #f3bc2b 0%, #eb3751 100%);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}@media(min-width: 768px){.textContent h2{font-size:2.25rem}}.textContent p{line-height:1.6;font-size:1rem}@media(min-width: 768px){.textContent p{font-size:1.125rem}}.textContent p .bold{font-weight:700}.carousel .keen-slider__slide{border-radius:.5rem;padding:1.5rem;margin:.5rem;transition:transform .25s ease,box-shadow .25s ease}.carousel .keen-slider__slide:hover{transform:translateY(-4px);box-shadow:0 8px 20px rgba(0,0,0,.08)}.carousel .keen-slider__slide .slideContent{display:flex;flex-direction:column;align-items:center;text-align:center;height:100%}.carousel .keen-slider__slide .slideContent .iconContainer{position:relative;width:60px;height:60px;margin-bottom:1rem}.carousel .keen-slider__slide .slideContent .cardTitle{margin-bottom:.5rem;font-weight:700;font-size:1.25rem}.carousel .keen-slider__slide .slideContent .cardDesc{font-size:.9375rem;line-height:1.4}